body{
    
    margin: 0;
    padding: 0;
}
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    
}

.container{
    display: grid;
    grid-template-columns: 1fr 5fr;
    grid-template-rows: auto 1fr;
    height: 100vh;
    background-color: #9BA595;
}

.leftpanel{
    grid-column: 1/2;
    border-right: 4px solid #689492;
    height: 100dvh;
    background-color: #8D9B95;
    
}

.main-title{
    display: flex;
    justify-content: center;
    margin-top: 1rem;
    margin-bottom: 1rem;
    font-family: "norsebold";
    font-size: 1.7rem;
    border-bottom: 2px dashed #1F1B21;
}

.new-task{
    display: flex;
    justify-content: center;
    margin-bottom: 1rem;
    font-family: "source_sans";
    cursor: pointer;
    font-weight: 700;
}

ul {
    list-style-type: none ;
    font-family: "sinkin_sans";
    
}

li > img{
    padding-right: 10px;
}
li > div{
    display: flex;
    align-items: center;
}

ul > li{
    display: flex;
    flex-direction: row;
    padding: 8px 0;
    cursor: pointer;
}
.upperpart{
    display: flex;
    flex-direction: column;
    padding-left: 1rem;
    border-bottom: 2px solid #1F1B21;
    border-top: 2px solid #1F1B21;
}

.headerproj{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin-bottom: 1rem;
    
}
.headerproj > .title{
    display: flex;
    align-items: center;
    margin: 1rem 1rem;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-weight: 700;
}
.project-item{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 1rem;
    padding-right: 1rem;
    font-family: "sinkin_sans";
    border-bottom: 2px solid #1c1e20;
    cursor: pointer;
}

.proj-items{
    display: flex;
    align-items: center;
    margin: 3rem;
    flex-direction: column;
    gap: 1rem;
    
}

.project-name-div{
    font-family: "sinkin_sans";
    width: 50%;
    display: flex;
    justify-content: center;
    margin: 1rem;
    border: 1px solid black;
    border-radius: 10px;
    padding: 5px;
    background-color: #8D9B95;
    transition: 1s font-size;
    cursor: pointer;
}

.project-name-div:hover{
    font-size: 1.2rem;
}
.main-content{
    grid-column:2/3 ;
    grid-row: auto 1fr;
}

.title-main{
    display: flex;
    justify-content: center;
    margin: 1rem;
    font-family: "source_sans";
    font-weight: 900;
    font-size: 2rem;
}

.todo-items{
    display: flex;
    flex-direction: column;
    align-items: center;
    
}

.card{
    background-color:#9FAEBC;
    margin: 1rem ;
    width: 50%;
    font-family: "source_sans";
    border-radius: 12px;
    border: 5px solid rgb(38, 102, 145);
    box-shadow: 2px 2px 5px rgba(16, 26, 117, 0.671);
    transition: 0.3s background-color;
    font-size: 1.1rem;
    
    
}
.card:hover{
    background-color: #727997;
}

.card-title{
    display: flex;
    justify-content: center;
    font-family: "sinkin_sans";
    font-size: 1.3rem;
    margin-bottom: 1rem;
}

.card-project-title-container{
    display: flex;
    flex-direction: row;
    padding-left: 2.2rem;
    padding-top: 0.3rem;
}

.title-text-proj{
    display: flex;
    align-items: center;
}

.card-project{
    margin-left: 0;
    padding-left: 2px;
}
.card-description, .card-priority, .card-duedate {
    padding-left: 2.2rem;
    padding-top: 0.3rem;
}

.card-images{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 1rem;
    padding: 1rem;
}

.message-div{
    display: flex;
    justify-content: center;
    margin-top: 5rem;
}
.delete-project-icon{
    width: 20px;
}

.card-circle-unchecked, .card-circle-checked, .delete-task{
    width: 20px;
}
.strikethrough {
    text-decoration: line-through;
}
