___CSS_LOADER_EXPORT___.push([module.id, `#dialog {
    border: none;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    justify-content: center;
    align-items: center;
    background-color: #79816a;
}

#form {
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    color: var(--title-color);
    font-weight: bold;
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-template-rows: 1fr 1fr 1fr 1fr 1fr 3fr;
    gap: 1rem;
}

#form > input {
    height: 1.5rem;
    padding: 0.5rem;
}

.buttons {
    align-self: center;
    display: flex;
    gap: 20px;
    grid-column: auto 1fr;

}

input {
    border: 1px solid var(--title-color);
    border-radius: 5px;
    color: rgb(88, 88, 255);
}

input:focus,
#priority:focus,
#projects:focus,
#notes:focus {
    outline: none;
}

#priority,
#notes {
    border: 1px solid var(--title-color);
    color:rgb(88, 88, 255);
    border-radius: 5px;
}

.projects-list input{
    height: 1.5rem;
    width: 100%;
}

#cancel,
#submit {
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    width: 100px;
    padding: 1px 5px;
    color: var(--title-color);
    font-weight: 1000;
    border: 1px solid var(--title-color);
    border-radius: 5px;
    background-color:transparent;
    cursor: pointer;
    transition: 0.3s background-color;
}

#cancel:hover,#submit:hover{
    background-color: blanchedalmond;
}

/* Center the dialog in the middle of the screen */
.new-project-dialog {
    border: none;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: rgb(124, 111, 106);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    padding: 2rem;
    z-index: 1000;
    min-width: 300px; /* Set a minimum width */
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

/* Form inside the dialog */
#new-project-form {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    width: 100%;
}

/* Input field styling */
#new-project-form input {
    height: 1.5rem;
    padding: 0.5rem;
    border: 1px solid var(--title-color);
    border-radius: 5px;
    color: rgb(88, 88, 255);
}

/* Focus state for inputs */
#new-project-form input:focus {
    outline: none;
    border-color: rgb(0, 122, 255);
}

/* Buttons container inside the form */
.dialog-buttons {
    display: flex;
    justify-content: space-between; /* Space out the buttons */
    gap: 10px;
}

/* Button styling */
.dialog-buttons button {
    width: 100px;
    padding: 5px 10px;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-weight: bold;
    color: var(--title-color);
    border: 1px solid var(--title-color);
    border-radius: 5px;
    background-color: transparent;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

/* Button hover effect */
.dialog-buttons button:hover {
    background-color: rgba(88, 88, 255, 0.1);
}
